body{
  background: rgb(242, 251, 252);
background: linear-gradient(90deg, rgb(246, 253, 253) 0%, rgb(241, 243, 247) 100%);
filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#EBF4F5", endColorstr="#B5C6E0", GradientType=1 );
}  

 img{
  
  height: 295px;
  
} 
.navbtn:hover{
  box-shadow: 0 0 5px 0 whitesmoke;
  border:1px white; 
  cursor: pointer;

}
img.d-block{
  width:192px;
  height:220px;
}
.nav{
  margin-left: 1%;
  
}
.box1{
  width:400px;
  margin-left: 33%;
  box-shadow: 0 0 5px 0 grey;
  background-color: white;
}
.box2{
  background-color: white;
}


img.pic{
  width:90px;
  height: 90px;
  margin:20px;
  border:1px solid rgb(175, 175, 175);
  border-radius: 10px;
  box-shadow: 0 0 2px 0 black;
  transition: 2ms  all;
  
}
img.pic:hover{
  cursor:pointer;
  border:5px solid lightcyan;
  box-shadow: 0 0 10px 0 purple;
}
.picture{
  display:flex;
  align-items: center;
  justify-content: center;
}


.carousel{
  margin-top: 5%;
}
h1{
  margin-left: 42%;
}

h2{
  
  display:flex;
  justify-content: center;
  align-items: center;
  font-family: 'PT Sans', sans-serif;
  margin-left: 3%;
}

h3{
  color:white;
  text-shadow: 2px 2px rgb(92, 92, 248);
  font-size: bold;
  margin-bottom: 25%;
  margin-left: 6%;
  font-family: 'PT Sans', sans-serif;
  background-color: rgba(3, 3, 3, 0.397);
  border:1px white;
  border-radius: 10px;
  box-shadow: 0 0 5px 0 black;
}
.abc{
  display:flex;
  justify-content: center;
  align-items: center;
  /* min-height: 1vh; */
}
input[type="radio"]{
display: none;
}
.starric{
  cursor: pointer;
  transition:color 200ms
}
.comments {
  margin-top: 20px;
}

.comments-title {
  font-size: 30px;
  margin-bottom: 20px;
}

.comments-container {
  margin-top: 40px;
}

.comment-form-title {
  font-size: 22px;
  font-family: 'PT Sans', sans-serif;
}

.comment-form-textarea {
  width: 99%;
  height: 80px;
  margin-bottom: 8px;
  margin-top: 8px;
  border:transparent;
  box-shadow: 0 0 10px 0 grey;
  border-radius: 10px;
  margin-left: 1%;
  cursor: pointer;
  font-family: 'PT Sans', sans-serif;

}
.comment-form-textarea:focus{
  outline:none;
}

.comment-form-button {
  font-size: 16px;
  padding: 8px 20px;
  background: rgb(59, 130, 246);
  border-radius: 8px;
  color: white;
  cursor: pointer;
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: rgb(37, 99, 235);
  box-shadow: 0 0 10px 0 grey;
}

.comment-form-button:disabled {
  opacity: 0.7;
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  margin-bottom: 28px;
  font-family: 'PT Sans', sans-serif;
}

.comment-image-container {
  margin-right: 12px;
}

.comment-image-container img {
  border-radius: 50px;
  width:40px;
  height:40px;
}

.comment-right-part {
  width:100%;


}


.comment-content {
  display: flex;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
}

.comment-text {
  font-size: 18px;
}

.comment-actions {
  display: flex;
  font-size: 12px;
  color: rgb(51, 51, 51);
  cursor: pointer;
  margin-top: 8px;
}

.comment-action {
  margin-right: 8px;
}

.comment-action:hover {
  text-decoration: underline;
}

.replies {
  margin-top: 20px;
}





.overlay {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 25vh;
	z-index: 100;

	
	background: linear-gradient(
		0deg,
		rgba(255, 255, 255, 1) 70%,
		rgba(255, 255, 255, 0.9) 75%,
		rgba(255, 255, 255, 0.25) 90%,
		rgba(255, 255, 255, 0) 95%
	);
}

.text {
	font-family:'PT Sans' , sans-serif;
  font-style:normal;
	font-size: 65px;
	display: flex;
	position: absolute;
	bottom: 12vh;
	left: 50%;
	transform: translateX(-50%);
	user-select: none;
}

.wrapper {
		padding-left: 20px;
		padding-right: 20px;
		padding-top: 20px;
}
.letter {
			transition: ease-out 1s;
			transform: translateY(40%);
}
.hadow {
			transform: scale(1, -1);
			color: #999;
			transition: ease-in 3s, ease-out 3s;
}
.wrapper:hover .letter {

				transform: translateY(-30%);
}
.wrapper:hover .hadow {
				opacity: 0;
				transform: translateY(30%);
			}
